"use client";

import { Store, MessageSquareLock, Sparkles, Headset } from "lucide-react";

export function Features() {
    const features = [
        {
            title: "Shop Local",
            description: "Discover and support creators and sellers in your campus community.",
            icon: Store,
            color: "text-primary",
            bgColor: "bg-primary/10"
        },
        {
            title: "Secure Chat",
            description: "Buy and sell with confidence using our secure, built-in messaging.",
            icon: MessageSquareLock,
            color: "text-emerald-500",
            bgColor: "bg-emerald-500/10"
        },
        {
            title: "Unique Finds",
            description: "Find one-of-a-kind items that match your unique style and personality.",
            icon: Sparkles,
            color: "text-primary",
            bgColor: "bg-primary/10"
        },
        {
            title: "24/7 Support",
            description: "Contact us anytime you want for any enquiries.",
            icon: Headset,
            color: "text-blue-500",
            bgColor: "bg-blue-500/10"
        }
    ];

    return (
        <section className="relative z-20 -mt-16 pb-12">
            <div className="container mx-auto px-4">
                <div className="bg-white text-gray-900 rounded-[2.5rem] shadow-2xl p-4 md:p-8 flex flex-col md:flex-row items-center justify-between gap-8 md:gap-4 border border-gray-100">
                    {features.map((feature, index) => (
                        <div
                            key={index}
                            className={`flex flex-1 items-center gap-4 p-4 rounded-3xl hover:bg-gray-50 transition-all duration-300 group ${index !== features.length - 1 ? 'md:border-r border-gray-100' : ''
                                }`}
                        >
                            <div className={`${feature.bgColor} ${feature.color} p-4 rounded-2xl group-hover:scale-110 transition-transform`}>
                                <feature.icon className="w-6 h-6" />
                            </div>
                            <div className="flex flex-col">
                                <h3 className="font-bold text-sm tracking-tight font-sans text-gray-900">{feature.title}</h3>
                                <p className="text-[11px] leading-relaxed text-gray-600 font-sans max-w-[140px]">{feature.description}</p>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
