"use client";

import { ChevronDown, Gift, LayoutGrid, Zap, Heart, Search, HelpCircle, Users2 } from "lucide-react";
import { ExploreDropdown } from "./ExploreDropdown";
import { BuzzDropdown } from "./BuzzDropdown";
import { QuickFindDropdown } from "./QuickFindDropdown";
import { SupportDropdown } from "./SupportDropdown";
import { useState } from "react";

export function NavBar() {
    const [isExploreOpen, setIsExploreOpen] = useState(false);
    const [buzzVisible, setBuzzVisible] = useState(false);
    const [quickVisible, setQuickVisible] = useState(false);
    const [supportVisible, setSupportVisible] = useState(false);

    return (
        <div className="bg-transparent pt-0 pb-4">
            <div className="container mx-auto px-4">
                <div className="relative max-w-6xl mx-auto">
                    <div className="bg-secondary text-white rounded-full px-2 py-2 flex items-center justify-between shadow-lg">

                        {/* Left Navigation */}
                        <div className="flex items-center">
                            {/* Explore trigger */}
                            <button
                                className="flex items-center gap-2 py-2 px-6 hover:text-primary transition-colors font-medium whitespace-nowrap"
                                onMouseEnter={() => setIsExploreOpen(true)}
                                onMouseLeave={() => setIsExploreOpen(false)}
                            >
                                <LayoutGrid className="w-4 h-4" />
                                <span>Explore</span>
                            </button>

                            <div className="h-6 w-px bg-white/10 mx-2"></div>

                            {/* Campus Buzz */}
                            <div
                                className="flex items-center gap-2 py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap relative group"
                                onMouseEnter={() => setBuzzVisible(true)}
                                onMouseLeave={() => setBuzzVisible(false)}
                            >
                                <Zap className="w-4 h-4 text-primary" />
                                <span className="font-sans font-medium text-sm">Campus Buzz</span>
                                <span className="bg-primary text-white text-[10px] px-1.5 py-0.5 rounded-full ml-1 font-bold">HOT</span>
                                <ChevronDown className="w-3 h-3 text-gray-400 group-hover:text-primary transition-colors" />

                                <BuzzDropdown isOpen={buzzVisible} />
                            </div>

                            {/* Campus Deals */}
                            <div className="flex items-center gap-2 py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap group">
                                <Heart className="w-4 h-4 text-primary" />
                                <span className="font-sans font-medium text-sm">Campus Deals</span>
                                <ChevronDown className="w-3 h-3 text-gray-400 group-hover:text-primary transition-colors" />
                            </div>

                            {/* Quick find */}
                            <div
                                className="flex items-center gap-2 py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap group relative"
                                onMouseEnter={() => setQuickVisible(true)}
                                onMouseLeave={() => setQuickVisible(false)}
                            >
                                <Search className="w-4 h-4 text-gray-400 group-hover:text-primary transition-colors" />
                                <span className="font-sans font-medium text-sm">Quick find</span>
                                <ChevronDown className="w-3 h-3 text-gray-400 group-hover:text-primary transition-colors" />

                                <QuickFindDropdown isOpen={quickVisible} />
                            </div>

                            {/* Support */}
                            <div
                                className="flex items-center gap-2 py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap group relative"
                                onMouseEnter={() => setSupportVisible(true)}
                                onMouseLeave={() => setSupportVisible(false)}
                            >
                                <HelpCircle className="w-4 h-4 text-gray-400 group-hover:text-primary transition-colors" />
                                <span className="font-sans font-medium text-sm">Support</span>
                                <ChevronDown className="w-3 h-3 text-gray-400 group-hover:text-primary transition-colors" />

                                <SupportDropdown isOpen={supportVisible} />
                            </div>

                            {/* Team */}
                            <div className="flex items-center gap-2 py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap group">
                                <Users2 className="w-4 h-4 text-gray-400 group-hover:text-primary transition-colors" />
                                <span className="font-sans font-medium text-sm">Team</span>
                            </div>
                        </div>

                        {/* Right CTA */}
                        <div className="hidden md:block pr-1">
                            <button className="bg-primary text-white font-bold py-2.5 px-6 rounded-full flex items-center gap-2 hover:bg-orange-600 transition-all duration-300 shadow-lg transform hover:-translate-y-0.5 font-sans">
                                <Gift className="w-5 h-5" />
                                <span>Get Rewarded</span>
                            </button>
                        </div>
                    </div>

                    {/* Dropdown positioned relative to navbar container */}
                    <div
                        onMouseEnter={() => setIsExploreOpen(true)}
                        onMouseLeave={() => setIsExploreOpen(false)}
                    >
                        <ExploreDropdown isOpen={isExploreOpen} />
                    </div>
                </div>
            </div>
        </div>
    );
}
